.button {
  border: none;
  display: inline-block;
}

.button__blue {
  font-family: "Inter Tight";
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color: #ffffff;
  padding: 10px 20px 10px;
  width: 280px;
  background-color: #18bbfe;
  border-radius: 6px;
  transition-duration: 500ms;
}

.button__blue:hover {
  transition-duration: 500ms;
  background-color: #d5edff;
  color: #18bbfe;
}
